// React core
import React from 'react'
import PropTypes from 'prop-types'
import { Route, Link, withRouter } from 'react-router-dom'
import { compose } from 'recompose'

// Styling
import { styled, withStyles } from '@material-ui/styles'
import classNames from 'classnames'
import { fade } from '@material-ui/core/styles/colorManipulator'
import withWidth, { isWidthDown } from '@material-ui/core/withWidth'

// Material UI
import AppBar from '@material-ui/core/AppBar'
import Toolbar from '@material-ui/core/Toolbar'
import IconButton from '@material-ui/core/IconButton'
import MenuIcon from '@material-ui/icons/Menu'
import MoreVertIcon from '@material-ui/icons/MoreVert'
import Drawer from '@material-ui/core/Drawer'
import ChevronLeftIcon from '@material-ui/icons/ChevronLeft'
import Divider from '@material-ui/core/Divider'
import Typography from '@material-ui/core/Typography'
import ListItem from '@material-ui/core/ListItem'
import ListItemIcon from '@material-ui/core/ListItemIcon'
import ListItemText from '@material-ui/core/ListItemText'
import SearchIcon from '@material-ui/icons/Search'
import SettingsIcon from '@material-ui/icons/Settings'
import InputBase from '@material-ui/core/InputBase'
import Chip from '@material-ui/core/Chip'
import Avatar from '@material-ui/core/Avatar'
import Badge from '@material-ui/core/Badge'
import NotificationsIcon from '@material-ui/icons/Notifications'
import ChangeColorIcon from '@material-ui/icons/InvertColors'
import HighlightIcon from '@material-ui/icons/Highlight'
import FormatTextdirectionRToLIcon from '@material-ui/icons/FormatTextdirectionRToL'
import CallMadeicon from '@material-ui/icons/CallMade'
import ExitToAppIcon from '@material-ui/icons/ExitToApp'

// Components
import Login from '../Login'
import Curriculum from '../Curriculum'

// Routes
import routes from '../../routes'

const leftDrawerWidth = 230
const styledBy = (property, mapping) => props => mapping[props[property]]
const leftBarStyles = theme => ({
  leftDrawerPaper: {
    position: 'relative',
    transition: theme.transitions.create('width', {
      duration: theme.transitions.duration.enteringScreen,
      easing: theme.transitions.easing.sharp
    }),
    whiteSpace: 'nowrap',
    width: leftDrawerWidth
  },
  leftDrawerPaperClose: {
    overflowX: 'hidden',
    transition: theme.transitions.create('width', {
      duration: theme.transitions.duration.leavingScreen,
      easing: theme.transitions.easing.sharp
    }),
    width: theme.spacing.unit * 7,
    [theme.breakpoints.up('sm')]: {
      width: theme.spacing.unit * 9
    }
  },
  rightDrawerInputRoot: {
    color: 'inherit',
    width: '100%'
  },
  rightDrawerInputInput: {
    padding: `
      ${theme.spacing.unit}px 
      ${theme.spacing.unit}px 
      ${theme.spacing.unit}px 
      ${theme.spacing.unit * 10}px
    `,
    transition: theme.transitions.create('width'),
    width: '100%',
    [theme.breakpoints.up('sm')]: {
      width: 120,
      '&:focus': {
        width: 200
      }
    }
  }
})

const Root = styled('div')({
  display: 'flex',
  height: '100vh'
})

const AppBarStyled = styled(({ leftBarOpened, ...other }) => <AppBar {...other} />)(theme => ({
  marginLeft: styledBy('leftBarOpened', {
    true: leftDrawerWidth,
    false: '0px'
  }),
  transition: styledBy('leftBarOpened', {
    true: theme.transitions.create(['width', 'margin'], {
      duration: theme.transitions.duration.enteringScreen,
      easing: theme.transitions.easing.sharp
    }),
    false: theme.transitions.create(['width', 'margin'], {
      duration: theme.transitions.duration.leavingScreen,
      easing: theme.transitions.easing.sharp
    })
  }),
  width: styledBy('leftBarOpened', {
    true: `calc(100% - ${leftDrawerWidth}px)`,
    false: '100%'
  }),
  zIndex: theme.zIndex.drawer + 1
}))

const ToggleBarButton = styled(({ barOpened, ...other }) => <IconButton {...other} />)({
  display: styledBy('barOpened', {
    true: 'none',
    false: 'block'
  }),
  marginLeft: 12,
  marginRight: 36
})

const Logo = styled('img')(theme => ({
  marginRight: 36,
  pointerEvents: 'none',
  [theme.breakpoints.down('md')]: {
    margin: 'auto'
  }
}))

const Grow = styled('div')(theme => ({
  flexGrow: 1,
  [theme.breakpoints.down('md')]: {
    flexGrow: 0
  }
}))

const RightDrawerMobileButton = styled('div')(theme => ({
  [theme.breakpoints.up('lg')]: {
    display: 'none'
  }
}))

const RightDrawer = styled('div')(theme => ({
  alignItems: 'center',
  display: 'flex',
  [theme.breakpoints.down('md')]: {
    display: 'none'
  }
}))

const Search = styled('div')(theme => ({
  borderRadius: theme.shape.borderRadius,
  backgroundColor: fade(theme.palette.common.white, 0.15),
  marginLeft: 0,
  position: 'relative',
  width: '100%',
  [theme.breakpoints.up('sm')]: {
    marginLeft: theme.spacing.unit,
    width: 'auto'
  },
  '&:hover': {
    backgroundColor: fade(theme.palette.common.white, 0.25)
  }
}))

const SearchIconBox = styled('div')(theme => ({
  alignItems: 'center',
  display: 'flex',
  height: '100%',
  justifyContent: 'center',
  pointerEvents: 'none',
  position: 'absolute',
  width: theme.spacing.unit * 9
}))

const RightDrawerToolbar = styled('div')(theme => ({
  alignItems: 'center',
  display: 'flex',
  justifyContent: 'space-between',
  paddingLeft: 2 * theme.spacing.unit,
  width: 400
}))

const ChipStyled = styled(Chip)(theme => ({
  backgroundColor: theme.palette.primary.main,
  border: '1.5px solid white',
  color: 'white',
  paddingLeft: 1
}))

const AvatarStyled = styled(Avatar)({
  width: 30,
  height: 30
})

const LeftDrawerToolbar = styled('div')(theme => ({
  alignItems: 'center',
  display: 'flex',
  justifyContent: 'flex-end',
  padding: `0px ${theme.spacing.unit}px`,
  ...theme.mixins.toolbar
}))

const SectionTitle = styled(Typography)(theme => ({
  fontWeight: 500,
  padding: theme.spacing.unit,
  paddingLeft: theme.spacing.unit * 2
}))

const Content = styled('main')(theme => ({
  flexGrow: 1,
  height: 'calc(100vh - 64px)',
  marginTop: 64,
  overflow: 'auto',
  padding: theme.spacing.unit * 4,
  width: '100%',
  [theme.breakpoints.down('md')]: {
    flexGrow: 0
  },
  [theme.breakpoints.down('sm')]: {
    padding: theme.spacing.unit * 2
  }
}))

class App extends React.Component {
  static propTypes = {
    classes: PropTypes.object.isRequired,
    width: PropTypes.string.isRequired
  }

  state = {
    leftBarOpened: true,
    rightBarOpened: false,
    isLogged: false,
    search: ''
  }

  componentDidMount () {
    if (!this.state.isLogged) {
      this.props.history.push(routes.login.form)
    }
  }

  componentWillMount () {
    if (isWidthDown('xs', this.props.width)) {
      this.setState({ leftBarOpened: false, rightBarOpened: false })
    }
  }

  toggleLeftBar = () => {
    this.setState(({ leftBarOpened: prevLeftBarOpened }) => ({
      leftBarOpened: !prevLeftBarOpened
    }))
  }

  closeBarsOnMobile = () => {
    if (isWidthDown('xs', this.props.width)) {
      this.setState({ leftBarOpened: false, rightBarOpened: false })
    }
  }

  handleLogin = () => {
    this.setState({
      isLogged: true
    })

    this.props.history.push(routes.logged.curriculum.home)
  }

  handleLogout = () => {
    this.setState({
      isLogged: false
    })

    this.props.history.push(routes.login.form)
  }

  handleSearch = (e) => {
    this.setState({ search: e.target.value })
  }

  render () {
    return (
      <Root style={!this.state.isLogged ?
        { alignItems: 'center', justifyContent: 'center' } :
        {}}>
        {this.state.isLogged ?
          <React.Fragment>
            <AppBarStyled
              position='fixed'
              leftBarOpened={this.state.leftBarOpened}
            >
              <Toolbar disableGutters={!this.state.leftBarOpened}>
                <ToggleBarButton
                  color='inherit'
                  aria-label='Open left drawer'
                  onClick={this.toggleLeftBar}
                  barOpened={
                    this.state.leftBarOpened
                    || this.state.rightBarOpened
                  }
                >
                  <MenuIcon />
                </ToggleBarButton>
                <Logo src='https://i.imgur.com/oXddTS4.png' alt='CIAPD Logo' />
                <Grow />
                <RightDrawerMobileButton>
                  <ToggleBarButton
                    color='inherit'
                    aria-label='Open right drawer'
                    onClick={/* toggleRightBar */() => alert('Em breve, essa opção estará disponível!')}
                    barOpened={
                      this.state.leftBarOpened
                      || this.state.rightBarOpened
                    }
                  >
                    <MoreVertIcon />
                  </ToggleBarButton>
                </RightDrawerMobileButton>
                <RightDrawer style={{ paddingRight: this.state.leftBarOpened ? 0 : 24 }}>
                  <Search>
                    <SearchIconBox>
                      <SearchIcon />
                    </SearchIconBox>
                    <InputBase
                      onKeyUp={this.handleSearch}
                      placeholder='Search…'
                      classes={{
                        root: this.props.classes.rightDrawerInputRoot,
                        input: this.props.classes.rightDrawerInputInput
                      }}
                    />
                  </Search>
                  <RightDrawerToolbar>
                    <ChipStyled
                      avatar={<AvatarStyled alt='Manuela' src='https://i.imgur.com/bHUpbYu.jpg' />}
                      label='Manuela Costa'
                    />
                    <IconButton color='inherit'>
                      <Badge badgeContent={17} color='error'>
                        <NotificationsIcon />
                      </Badge>
                    </IconButton>
                    <IconButton color='inherit'>
                      <ChangeColorIcon />
                    </IconButton>
                    <IconButton color='inherit'>
                      <HighlightIcon />
                    </IconButton>
                    <IconButton color='inherit'>
                      <FormatTextdirectionRToLIcon />
                    </IconButton>
                    <IconButton color='inherit'>
                      <CallMadeicon />
                    </IconButton>
                  </RightDrawerToolbar>
                </RightDrawer>
              </Toolbar>
            </AppBarStyled>
            <Drawer
              variant='permanent'
              open={this.state.leftBarOpened}
              classes={{
                paper: classNames(
                  this.props.classes.leftDrawerPaper,
                  !this.state.leftBarOpened
                  && this.props.classes.leftDrawerPaperClose
                )
              }}
            >
              <LeftDrawerToolbar>
                <IconButton onClick={this.toggleLeftBar}>
                  <ChevronLeftIcon />
                </IconButton>
              </LeftDrawerToolbar>
              <Divider />
              <SectionTitle
                variant='body1'
                noWrap
              >
                Curriculum
              </SectionTitle>
              <ListItem
                onClick={this.closeBarsOnMobile}
                component={Link}
                button to={routes.logged.curriculum.home}
              >
                <ListItemIcon>
                  <SearchIcon />
                </ListItemIcon>
                <ListItemText primary='Consultar' />
              </ListItem>
              <Divider />
              <SectionTitle
                variant='body1'
                noWrap
              >
                Configurações
              </SectionTitle>
              <ListItem
                disabled
                onClick={this.closeBarsOnMobile}
                component={Link}
                button to={routes.logged.dashboard}
              >
                <ListItemIcon>
                  <SettingsIcon />
                </ListItemIcon>
                <ListItemText primary='Minha conta' />
              </ListItem>
              <ListItem
                onClick={this.handleLogout}
                button
              >
                <ListItemIcon>
                  <ExitToAppIcon />
                </ListItemIcon>
                <ListItemText primary='Sair' />
              </ListItem>
            </Drawer>
            {
              isWidthDown('xs', this.props.width) && (this.state.leftBarOpened || this.state.rightBarOpened) ?
                <div />
                : <Content>
                  <Route exact path={routes.logged.curriculum.home} component={() => <Curriculum search={this.state.search} />} />
                </Content>
            }
          </React.Fragment> : <React.Fragment>
            <Route
              exact
              path={routes.login.form}
              component={
                () => <Login
                  screen='Form'
                  pWidth={this.props.width}
                  fLogin={this.handleLogin}
                />
              }
            />
            <Route
              exact
              path={routes.login.lostPassword}
              component={
                () => <Login
                  screen='LostPassword'
                  pWidth={this.props.width}
                  fLogin={this.handleLogin}
                />
              }
            />
            <Route
              exact
              path={routes.login.readMore}
              component={
                () => <Login
                  screen='ReadMore'
                  pWidth={this.props.width}
                  fLogin={this.handleLogin}
                />
              }
            />
          </React.Fragment>}
      </Root>
    )
  }
}

export default withRouter(compose(
  withWidth(),
  withStyles(leftBarStyles)
)(App))
