// React core
import React from 'react'
import PropTypes from 'prop-types'
import { Link } from 'react-router-dom'

// Styling
import { withStyles } from '@material-ui/core/styles'
import styles from './styles'

// Components
import Typography from '@material-ui/core/Typography'
import FormControl from '@material-ui/core/FormControl'
import InputLabel from '@material-ui/core/InputLabel'
import Input from '@material-ui/core/Input'
import InputAdornment from '@material-ui/core/InputAdornment'
import Button from '@material-ui/core/Button'

// Icons
import AccountIcon from '@material-ui/icons/AccountCircle'
import PasswordIcon from '@material-ui/icons/VpnKey'

// Routes
import routes from '../../../routes'

class LoginForm extends React.Component {
  static propTypes = {
    classes: PropTypes.object.isRequired,
    fLogin: PropTypes.func.isRequired
  }

  render () {
    const {
      classes: pClasses,
      fLogin
    } = this.props

    return (
      <main>
        <div>
          <Typography className={pClasses.title} variant='h5'>
            Entrar
          </Typography>
          <Typography className={pClasses.subtitle} variant='subtitle1'>
            Empresa
          </Typography>
        </div>
        <form className={pClasses.signin} id='form-login'>
          <FormControl className={pClasses.email}>
            <InputLabel htmlFor='input-email'>
              Digite o seu e-mail
            </InputLabel>
            <Input
              id='input-email'
              type='email'
              placeholder='E-mail'
              startAdornment={
                <InputAdornment position='start'>
                  <AccountIcon />
                </InputAdornment>
              }
            />
          </FormControl>
          <FormControl className={pClasses.password}>
            <InputLabel htmlFor='input-password'>
              Digite a sua senha
            </InputLabel>
            <Input
              id='input-password'
              type='password'
              placeholder='Senha'
              startAdornment={
                <InputAdornment position='start'>
                  <PasswordIcon />
                </InputAdornment>
              }
            />
          </FormControl>
          <Typography className={pClasses.lostpasswordbox} color='primary' variant='body2'>
            <Link className={pClasses.lostpassword} to={routes.login.lostPassword}> Esqueceu sua senha? </Link>
          </Typography>
        </form>
        <div className={pClasses.signup}>
          <Typography variant='body2'>
            Não possui uma conta para o acesso? Participe das oficinas do CIAPD!
              <Link className={pClasses.readmore} to={routes.login.readMore}> Saiba mais</Link>
          </Typography>
        </div>
        <div className={pClasses.submit}>
          <Button variant='contained' color='primary' onClick={fLogin}> Entrar </Button>
        </div>
      </main>
    )
  }
}

export default withStyles(styles)(LoginForm)
