// React core
import React from 'react'
import PropTypes from 'prop-types'

// Styling
import { isWidthDown } from '@material-ui/core/withWidth'
import { withStyles } from '@material-ui/core/styles'
import styles from './styles'

// Components
import Paper from '@material-ui/core/Paper'
import LoginForm from './LoginForm'
import LostPassword from './LostPassword'
import ReadMore from './ReadMore'

// Media
import ciapdBlue from '../../media/ciapd_blue.png'

class Login extends React.Component {
  static propTypes = {
    pWidth: PropTypes.string.isRequired,
    classes: PropTypes.object.isRequired,
    screen: PropTypes.string.isRequired,
    fLogin: PropTypes.func.isRequired
  }

  render () {
    const {
      pWidth,
      classes: pClasses,
      screen,
      fLogin
    } = this.props

    const paperSize = isWidthDown('xs', pWidth) ? ({
      height: '100%',
      width: '100%'
    }) : ({
      height: 512,
      width: 440
    })

    return (
      <Paper style={paperSize} className={pClasses.container}>
        <header className={pClasses.logobox}>
          <img className={pClasses.logoimage} src={ciapdBlue} alt='CIAPD Logo' title='CIAPD Logo' />
        </header>
        {
          screen === 'Form' ?
            <LoginForm fLogin={fLogin} /> :
            screen === 'LostPassword' ?
              <LostPassword /> :
              screen === 'ReadMore' ?
                <ReadMore /> : <div>Invalid screen property.</div>
        }
      </Paper>
    )
  }
}

export default withStyles(styles)(Login)
